html {
	&.is-open-menu {
		overflow-y: scroll; } }

body {
	@include fr;
	@include f(16px,400,2.0625);
	color: #627791;
	background-color: #F2F8FF;
	.is-open-menu & {
		position: fixed;
		height: 100vh;
		width: 100%;
		overflow: hidden; } }

.title-lg {
	color: #000;
	@include f(84px,700,1.07);
	@include md {
		@include f(54px,700,1.6); } }
.title-md {
	color: #000;
	@include f(50px,700,1.2);
	@include md {
		@include f(30px,700,1.26); } }
.title {
	color: #000;
	@include f(36px,700,1.5); }

.description {
	@include f(18px,500,2.2); }

.color-blue {
	color: #3852CE; }

.text-center {
	text-align: center; }

.section {
	position: relative;
	padding: 178px 0;
	&_about {
		padding-bottom: 0; }
	&__wrap {
		max-width: 488px; }
	&__row {
		margin: 0 -10px;
		display: flex;
		&:not(:first-child) {
			margin-top: 103px; } }
	&__img {
		padding: 0 10px;
		width: 50%;
		img,
		svg {
			display: block;
			max-width: 100%; }
		&_shad {
			img {
				border-radius: 5px;
				box-shadow: 0 20px 40px 0 rgba(107,154,212,0.10); } }
		&_fixw {
			img {
				max-width: 960px; } }
		&_center {
			justify-content: center;
			display: flex; } }
	&__content {
		padding: 38px 10px 0;
		width: 50%;
		&_pt0 {
			padding-top: 0; }
		p {
			&:not(:first-child) {
				margin-top: 17px; }
			&:not(:last-child) {
				margin-bottom: 29px; } }
		.description + .btn {
			margin-top: 29px; } }
	.title {
		margin-bottom: 11px; }
	.description {
		margin-bottom: 15px; }

	.poligon-right {
		position: absolute;
		top: 50%;
		right: -10px;
		margin-top: -120px;
		width: 160px;
		img {
			display: block;
			max-width: 100%; } }
	.poligon-left {
		position: absolute;
		top: 40%;
		left: 0;
		width: 160px;
		img {
			display: block;
			max-width: 100%; } }
	.right-circle {
		position: absolute;
		z-index: 2;
		top: 30px;
		right: -10px; } }

.md-show {
	display: none !important;
	@include md {
		display: block !important; } }
.md-hide {
	@include md {
		display: none; } }
