.header {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 10;
	padding: 33px 0 27px;
	color: #627791;
	@include md {
		padding: 20px 0; }
	&__inner {
		margin: 0 auto;
		padding: 0 20px;
		max-width: 1600px;
		width: 100%;
		flex-wrap: wrap;
		align-items: center;
		display: flex;
		@include md {
			justify-content: space-between; } }
	&__logo {
		position: relative;
		margin-right: 40px;
		flex-shrink: 0;
		z-index: 2; }
	&__wrap {
		flex-grow: 1;
		align-items: center;
		justify-content: space-between;
		display: flex;
		@include md {
			position: absolute;
			top: -20px;
			left: 0;
			width: 100%;
			height: 100vh;
			padding: 124px 77px;
			overflow-y: auto;
			@include fadeout(.3s);
			background-color: rgba($black, .7);
			display: block; }
		.is-open-menu & {
			@include md {
				@include fadein; } } }
	&__nav {
		padding-left: 40px;
		position: relative;
		@include md {
			margin: 53px 0;
			padding-left: 0;
			color: #fff;
			text-align: center; }
		&:before {
			position: absolute;
			content: '';
			top: 0;
			left: 0;
			bottom: 0;
			width: 1px;
			background-color: rgba(#657180, .15); } }
	&__right {
		align-items: center;
		flex-wrap: wrap;
		display: flex;
		@include md {
			flex-direction: column; }
		.lang {
			&:not(:first-child) {
				margin-left: 10px;
				@include lg {
					margin-left: 0; } } } }
	.btn {
		@include md {
			width: 100%; }
		&:not(:last-child) {
			margin-right: 20px;
			@include md {
				margin: 0 0 15px 0; } } } }
.logo {
	width: 148px;
	svg {
		display: block;
		max-width: 100%;
		height: auto; }
	&__letter {
		transition: fill .3s;
		.is-open-menu & {
			@include md {
				fill: #fff; } } } }

.nav {
	text-transform: uppercase;
	font-size: 14px;
	font-weight: 700;
	letter-spacing: 0.08em;
	@include lg {
		font-size: 13px; }
	&__list {
		flex-wrap: wrap;
		display: flex;
		@include md {
			flex-direction: column; } }
	li {
		&:not(:last-child) {
			margin-right: 40px;
			@include md {
				margin: 0 0 27px 0; } } }
	a {
		color: currentColor;
		opacity: .7;
		transition: opacity .3s, color .3s;
		@include md {
			opacity: 1; }
		@include hover {
			opacity: 1; }
		&.is-active {
			opacity: 1;
			color: #000;
			@include md {
				color: #fff; } } } }


.lang {
	position: relative;
	color: #627791;
	font-size: 14px;
	font-weight: 500;
	user-select: none;
	.header & {
		@include md {
			color: #fff; } }
	&.is-open {
		.lang__list {
			@include fadein; }
		.lang__current:before {
			@include rotate(180); } }
	img {
		margin: 5px 10px 0 0;
		max-width: 18px;
		max-height: 18px;
		display: block; }
	&_top {
		.lang__list {
			top: auto;
			bottom: 100%;
			max-height: 100px;
			overflow-y: auto;
			left: 0; } }
	&__current {
		position: relative;
		display: flex;
		width: 100%;
		cursor: pointer;
		padding-right: 13px;
		&:before {
			position: absolute;
			content: '';
			top: 50%;
			right: 0;
			margin-top: -2px;
			transition: transform .3s;
			@include arr(8,4,#627791,b);
			.header & {
				@include md {
					@include arr(8,4,#fff,b); } } } }
	&__list {
		position: absolute;
		right: 0;
		top: 100%;
		padding-top: 5px;
		min-width: 90px;
		max-width: 120px;
		@include fadeout(.3s);
		@include prevent-text;
		li {
			display: block;
			&:not(:first-child) {
				margin-top: 5px; } }
		a {
			display: flex;
			color: currentColor;
			transition: opacity .3s;
			@include hover {
				opacity: .7; }
			img {
				margin-top: 5px; } } } }

.btn-menu {
	position: relative;
	z-index: 2;
	width: 17px;
	height: 16px;
	background-color: transparent;
	display: none;
	@include md {
		vertical-align: top;
		display: inline-block; }
	&:before {
		position: absolute;
		content: '';
		top: -5px;
		right: -5px;
		bottom: -5px;
		left: -5px; }
	&__line {
		position: absolute;
		width: 17px;
		left: 0;
		border-bottom: 2px solid $black;
		border-radius: 2px;
		transition: transform .3s, opacity .3s, border-color .3s;
		backface-visibility: hidden;
		will-change: transform;
		.header_abs & {
			border-color: $white; }
		.is-open-menu & {
			border-color: $white; }
		&:first-child {
			top: 0;
			transform-origin: top left;
			.is-open-menu & {
				transform: rotate(45deg) translate(1px, 1px); } }
		&:nth-child(2) {
			right: 0;
			left: auto;
			top: 7px;
			width: 12px;
			transform-origin: left;
			.is-open-menu & {
				opacity: 0;
				transform: scaleX(0); } }
		&:nth-child(3) {
			bottom: 0;
			transform-origin: bottom left;
			.is-open-menu & {
				transform: rotate(-45deg) translate(1px, -1px); } } } }
